/* footer */
.footer{
color: white;
/* padding: 25px;    */
background-color: black;
}
.footer a{
    text-decoration: none;
    color: rgb(133, 133, 133);
    padding: 10px;
}
.footer a:hover{
    color: white;
    border-bottom: 1px solid white;
    transition: 0.5s ease;
}