.gallery{
    -webkit-column-count:5;
    -moz-column-count: 5;
    column-count: 5;
    -webkit-column-width: 33%;
    -moz-column-width: 33%;
    column-width: 33%;
    padding: 0 10px;
}
.gallery .pics{
    -webkit-transition: all 350ms ease;
    transition: all 350ms ease;
    margin-bottom: 12px;
}

@media (max-width:991px){
    .gallery{
        -webkit-column-count:3;
        -moz-column-count: 3;
        column-count: 3;
    }
}

